import React, { useState, useEffect } from 'react';
import { Fetch, daysSince } from '../../Utils.js';
import { useTranslation } from 'react-i18next';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js';
ChartJS.register();

export const RainfallByMonth = (props) => {
  const { t } = useTranslation();
  const [data, setData] = useState([]);
  
  useEffect(() => {
    Fetch("/api/data/rain", "GET", null, 
      (data) => {
        setData(aggregateData(data));
      })
  }, []);

  const aggregateData = (rawData) => {
    const values = [];
    for(let i=0; i < rawData.length; i++) {
      const yearMonth = rawData[i].date.slice(0, 7);
      const found = values.find(value => value.month === yearMonth);
      if (found === undefined) {
        values.push({"month":yearMonth, "amount":rawData[i].amount});
      } else {
        found.amount += rawData[i].amount;
      }
    }
    values.sort((a, b) => a.momth - b.month);
    return values;
  }

  const thedata = {
    labels: data.map(value => value.month),
    datasets: [{
      data: data.map(value => value.amount),
      backgroundColor: ["#65655E", "#7D80DA", "#B0A3D4", "#CEBACF", "#C6AFB1"],
    }],
  };

  const options = {
    plugins: {
      legend: {
        display:false,
      },
    },
  };

  return (
    <div className="Chart">
      <h3>{t("rainfall_by_month")}</h3>
      <Bar 
        data={thedata} 
        options={options}
        width={300}
        height={300}
      />
    </div>
  );
}
export default RainfallByMonth;
