import DataCollection from './DataCollection';
import { useTranslation } from 'react-i18next';

export const ListBreeds = (props) => {
  const systemAccount = "000000000000000000000000";
  const { t } = useTranslation();
  const collection = 'breeds';
  const columns = [
    {name: t("breed"),      selector: row => row.name},
    {name: t("nicknames"),  selector: row => row.matches},
    {
      name: t("breed_type"),
      selector: row => row.account === systemAccount ?
        t("breed_type_system") : t("breed_type_custom"),
      sortable: true,
    },
    {name: t("created_by"), selector: row => row.created_by},
  ];
  return (
    <DataCollection
      collection={collection}
      columns={columns}
      sortField={3}
    />
  );
}

export default ListBreeds;
