import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Fetch } from './Utils.js';

export const BreedDropdown = (props) => {
  const { t } = useTranslation();
  const [breeds, setBreedData] = useState([]);

  useEffect(() => {
    if (props.breeds != null) {
      setBreedData(props.breeds);
      return;
    }
    Fetch("api/data/breeds", "get", null,
      (data) => {
        setBreedData(data);
      },
      () => {console.log("error loading breeds")}
    );
  }, [props.breeds]);

  const Option = (props) => {
    if (props.selected === props.value) {
      return <option value={props.value} selected="selected">{props.name}</option>
    }
    return <option value={props.value}>{props.name}</option>
  }

  return (
    <select name="breed" onChange={props.onChange}
            style={props.style} value={props.selected}>
      {props.showAll &&
       <option value="">{t("all_breeds")}</option>
      }
      {!props.selected && !props.showAll &&
       <option value="" disabled selected hidden>-- {t("breed")} --</option>
      }
      {breeds.map((breed) => (
        <Option key={breed.name}
          value={breed.name}
          name={breed.name}
          selected={props.selected}
        />
      ))}
    </select>
  );
}

export default BreedDropdown;
